:root {
  --background-light: linear-gradient(120deg, rgba(224, 242, 254, 0.9), rgba(255, 255, 255, 0.9));
  --background-dark: linear-gradient(120deg, rgba(18, 18, 18, 0.9), rgba(38, 38, 38, 0.9));
  --card-bg-light: rgba(255, 255, 255, 0.95);
  --card-bg-dark: rgba(38, 38, 38, 0.95);
  --text-light: #666;
  --text-dark: #fff;
  --button-gradient-light: linear-gradient(45deg, #2196f3 30%, #64b5f6 90%);
  --button-gradient-dark: linear-gradient(45deg, #1565c0 30%, #1976d2 90%);
  --footer-bg-light: rgba(255, 255, 255, 0.95);
  --footer-bg-dark: rgba(38, 38, 38, 0.95);
}

.login-container {
  background: var(--background-light),
              url('../images/background-university.jpg');
  background-size: cover;
  background-position: center;
  min-height: 120vh;
  position: relative;
  transition: all 0.3s ease;
  padding-bottom: 200px;
}

.dark-mode .login-container {
  background: var(--background-dark),
              url('../images/background-university.jpg');
  background-size: cover;
  background-position: center;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.login-card {
  background: var(--card-bg-light) !important;
  backdrop-filter: blur(10px);
  border-radius: 15px !important;
  box-shadow: 0 15px 25px rgba(0, 149, 255, 0.1) !important;
  margin-bottom: 100px !important;
  transition: all 0.3s ease !important;
  border: 1px solid rgba(179, 216, 255, 0.3) !important;
}

.dark-mode .login-card {
  background: var(--card-bg-dark) !important;
  border-color: rgba(255, 255, 255, 0.1) !important;
}

.login-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 30px rgba(0, 149, 255, 0.15) !important;
}

.logo-container {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.logo-image {
  max-width: 400px;
  height: auto;
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.input-field {
  margin: 16px 0 !important;
  transition: all 0.3s ease !important;
  position: relative;
}

.input-field .MuiOutlinedInput-root,
.input-field .MuiInputLabel-root {
  transition: transform 0.3s ease !important;
}

.input-field:hover .MuiOutlinedInput-root {
  transform: translateX(5px);
}

.input-field:hover .MuiInputLabel-root:not(.MuiInputLabel-shrink) {
  transform: translateX(5px);
}

.input-field .MuiInputLabel-shrink {
  transform: translate(14px, -9px) scale(0.75);
}

.input-field:hover .MuiInputLabel-shrink {
  transform: translate(19px, -9px) scale(0.75);
}

.input-field .MuiOutlinedInput-notchedOutline {
  border-color: #90caf9 !important;
  transition: all 0.3s ease !important;
}

.dark-mode .input-field .MuiInputLabel-root {
  color: #90caf9 !important;
}

.dark-mode .input-field .MuiOutlinedInput-root {
  color: #fff !important;
}

.input-field .MuiOutlinedInput-root.Mui-focused {
  box-shadow: 0 0 0 1px rgba(144, 202, 249, 0.2);
}

.dark-mode .input-field .MuiOutlinedInput-root.Mui-focused {
  box-shadow: 0 0 0 1px rgba(144, 202, 249, 0.4);
}

.input-field .MuiInputLabel-root.Mui-focused {
  color: var(--primary-color, #2196F3) !important;
}

.dark-mode .input-field .MuiInputLabel-root.Mui-focused {
  color: #90caf9 !important;
}

.submit-button {
  background: var(--button-gradient-light) !important;
  transition: all 0.3s ease !important;
  text-transform: none !important;
  font-size: 1.1rem !important;
  padding: 12px !important;
  border-radius: 10px !important;
  box-shadow: 0 4px 15px rgba(33, 150, 243, 0.3) !important;
}

.dark-mode .submit-button {
  background: var(--button-gradient-dark) !important;
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(33, 150, 243, 0.4) !important;
}

.footer {
  position: fixed;
  bottom: -100%; /* Commence caché en dessous de la vue */
  left: 0;
  right: 0;
  background: var(--footer-bg-light);
  backdrop-filter: blur(10px);
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  border-top: 1px solid rgba(144, 202, 249, 0.2);
  z-index: 1000;
  transition: bottom 0.3s ease-in-out; /* Animation douce */
}

.footer.visible {
  bottom: 0; /* Apparaît en remontant */
}

.dark-mode .footer {
  background: var(--footer-bg-dark);
  color: var(--text-dark);
}

.footer-section {
  display: flex;
  flex-direction: column;
  gap: 8px;
  transition: transform 0.3s ease;
  padding: 0 20px;
}

.footer-section:hover {
  transform: translateY(-5px);
}

.footer-title {
  font-weight: bold !important;
  color: #1976d2 !important;
  margin-bottom: 8px !important;
  position: relative;
  font-size: 1.1rem !important;
}

.dark-mode .footer-title {
  color: #90caf9 !important;
}

.footer-title::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background: #2196f3;
  transition: width 0.3s ease;
}

.footer-section:hover .footer-title::after {
  width: 100%;
}

.footer-link {
  color: #42a5f5;
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  padding-left: 0;
}

.footer-link:hover {
  color: #1976d2;
  padding-left: 10px;
}

.dark-mode .footer-link {
  color: #90caf9;
}

.dark-mode .footer-link:hover {
  color: #bbdefb;
}

.social-icons {
  display: flex;
  gap: 12px;
}

.social-icons a {
  transition: transform 0.3s ease;
  color: #1976d2;
}

.dark-mode .social-icons a {
  color: #90caf9;
}

.social-icons a:hover {
  transform: scale(1.2);
}

.developer-name {
  font-style: italic;
  font-weight: 500;
  color: #1976d2;
  margin-top: 4px;
}

.dark-mode .developer-name {
  color: #90caf9;
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 10px;
  border-radius: 50%;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.theme-toggle:hover {
  transform: scale(1.1);
}

.dark-mode .theme-toggle {
  color: white;
}

.typing-text {
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  animation: typing 3.5s steps(40, end);
  margin: 0 auto;
  letter-spacing: .15em;
}

.typing-text::after {
  content: '';
  position: absolute;
  right: -3px;
  width: 3px;
  height: 100%;
  background: var(--primary-color, #2196F3);
  animation: typing-cursor 3.5s steps(40, end);
  opacity: 0;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes typing-cursor {
  0% { opacity: 1 }
  90% { opacity: 1 }
  100% { opacity: 0 }
}

.dark-mode .typing-text {
  color: #fff;
}

.dark-mode .typing-text::after {
  background: #90caf9;
}